a{
  display: block;
  text-decoration: none;
  font-weight: 400;
}
.frame{
  width: 100%;
  padding-top: 60px;
  min-height: 100vh;
  background: #f4f7ff;
}
.container{
  width: 95%;
  margin: 0 auto;
  padding: 0 40px;
  position: relative;
}
.headerInner{
  height: 100%;
  display: flex;
  align-items: center;
  gap: 20px;
}
.headerInner .logo{
  font-size: 24px;
  font-weight: 600;
  margin-right: 24px;
}
.headerInner a{
  font-size: 18px;
  font-weight: 500;
  
}
.h100{
  position: relative;
  height: 100%;
}
header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  box-shadow: 0 0 5px rgba(0,0,0,0.4);
  background: #FFF;
  z-index: 5;
}
.page{
  padding: 60px 0;
}
.page.main{
  padding-top: 0;
}
.visual{
  display: flex;
  flex-direction: column;
  gap: 30px;
  min-height: 240px;
  justify-content: center;  
}
.visualBg{
  background: #d1e7ff;
}
.visualBg *{
  color: #04405e;
}
.visualBg .title{
  font-size: 30px;
  font-weight: 500;
}
.visualBg .exp{
  font-size: 23px;
  font-weight: 400;
}
.topSection{
  padding-top: 30px;
  font-size: 22px;
  line-height: 30px;
}
.topSection .welcome{
  font-weight: 500;
}
.topSection .welcome .number,
.topSection .welcome .user{
  color: #000f7e;
  font-weight: 600;
}
.projectWrap{
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.projectCardWrap{
  padding: 20px;
  box-shadow: 0 0 2px rgba(0,0,0,0.4);
  border-radius: 6px;
  background: #fff;
  transition: background 0.4s;
}
.projectCardWrap .title{
  font-size: 26px;
  line-height: 32px;
  margin-bottom: 10px;
  font-weight: 500;
}
.projectCardWrap:hover{
  background: #f5f5f5;
}
.projectCardWrap .range{
  font-size: 17px;
  line-height: 26px;
  margin-bottom: 10px;
}
.projectCardWrap .emergencyMessage{
  font-size: 22px;
  line-height: 30px;
  color: #ff2525;
}
.projectCardWrap .notice{
  font-size: 22px;
  line-height: 30px;
}
.projectCardWrap .todoList{
  font-size: 22px;
  line-height: 30px;
}
.messageWrap{
  display: flex;
  flex-direction: column;
}
.messageWrap .messages{
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.messageWrap .messages:last-child .cardNotice:last-child{
  margin-bottom: 0;
}
.cardNotice:last-child{
  margin-bottom: 25px;
}

.icon {
  margin-right: 8px;
}
.cardNotice{
  display: flex;
}
.cardNotice svg{
  margin-top: 5px;
}
.noticeText{
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.emergencyMessage svg {
  fill: red;
  color: red;
}

.notice i {
  color: blue;
}

.todoList i {
  color: green;
}

.errorPage{
  min-height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  gap: 24px;
  justify-content: center;
  align-items: center;
  font-size: 34px;
  font-weight: 500;
}
.errorPage a{
  background: #0e0246;
  padding: 12px 32px;
  color: #FFF;
  border-radius: 6px;
}

.subPage{
  padding: 30px 0;
}
.subPage .title{
  font-size: 38px;
  line-height: 50px;
  font-weight: 500;
}
.projectDetail .range{
  font-size: 24px;
  line-height: 32px;
}
.projectDetail .noticeWrap{
  margin-top: 20px;
}
.projectDetail .noticeWrap .noticeType{
  font-size: 27px;
  font-weight: 500;
  line-height: 40px;
}
.projectDetail .noticeWrap .noticeItems{
  margin-bottom: 12px;
  margin-top: 5px;
}
.projectDetail .noticeWrap .noticeItems .noticeItem{
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
}
.projectDetail .noticeWrap .noticeItems .noticeItem .assignee{
  font-size: 20px;
  margin-left: 10px;
}
.projectDetail .noticeWrap .noticeItems .noticeItem button{
  position: relative;
  top: -3px;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-left: 8px;
  outline: none;
  border: 1px solid #808080;
  background: #FFF;
  cursor: pointer;
}
.gray{
  color: #6d6d6d;
}
.addNotice{
  display: flex;
  margin-bottom: 15px;
}
.addNotice input{
  width: 100%;
  border-radius: 0;
  outline: none;
  border: 1px solid #CCC;
  border-right: 0;
  height: 40px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 16px;
}
.addNotice select{
  width: 120px;
  outline: none;
  border: 1px solid #CCC;  
  flex-shrink: 0;
}
.addNotice button{
  width: 100px;
  height: 40px;
  flex-shrink: 0;
  border-radius: 0;
  border-left: 0;
  outline: none;
  border: 0;
  background: #220d81;
  color: #FFF;
  font-size: 16px;
  font-weight: 500;
}
.subTitle{
  font-size: 34px;
  font-weight: 600;
}
.userList{
  margin-top: 42px;
}
.userList .userItem{
  font-size: 22px;
  line-height: 50px;
  border: 1px solid #CCC;
  background: #FFF;
  border-bottom: 0;
  padding: 0 12px;
}
.userList .userItem:last-child{
  border-bottom: 1px solid #CCC;
}
.userList .addUser{
  margin: 20px 0 20px;
  display: flex;
}
.userList .addUser input{
  width: 100%;
  height: 50px;
  padding: 0 12px;
  box-sizing: border-box;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  border-left: 0;
  border-right: 0;
  outline: none;
}
.userList .addUser button{
  width:140px;
  height: 50px;
  cursor: pointer;
  border: 0;
  background: #555;
  color: #FFF;
  outline: none;
}
.userList .addUser select{
  width: 300px;
  /* appearance: none; */
  position: relative;
  cursor: pointer;
  border: 1px solid #ccc;
  outline: none;
  padding: 0 12px;
  font-size: 18px;
}
.userList .addUser select::after{
  content: ">";
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 24px;
  z-index: 555;
}

.mywork{

}
.mywork .pjtTitle{
  font-size: 32px;
  font-weight: 600;
}
.mywork .workContent{
  margin: 10px 0 30px;
}
.mywork .workContent .noticeItem{
  font-size: 24px;
  line-height: 40px;
}
.mywork .workContent .noticeItem:not(.type3){
  display: none;
}
.mywork .workContent .noticeItem.noneType{
  display: block;
  color: #888888;
}
.myWorkTitle{
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 40px;
}

.newProject .title{
  font-size: 32px;
  font-weight: 500;
}

.newProject input{
  width: 100%;
  height: 40px;
  font-size: 16px;
  display: block;
  margin-top: 20px;
  outline: none;
  padding: 0 12px;
  border: 1px solid #CCC;
  box-sizing: border-box;
}
.newProject select{
  width: 100%;
  height: 40px;
  font-size: 16px;
  display: block;
  margin-top: 10px;
  outline: none;
  padding: 0 12px;
  border: 1px solid #CCC;
  box-sizing: border-box;
}
.newProject .addProject{
  margin-top: 20px;
  background: #000f7e;
  box-sizing: border-box;
  padding: 14px;
  display: block;
  margin: 20px auto 0;
  color: #FFF;  
  font-size: 18px;
  font-weight: 600;
  outline: none;
  border: 0;
  cursor: pointer;
}